.nav-pills {
    font-size: 13px;
    cursor: pointer;
    font-family: 'Patua One', cursive;
}

/* .nav-link:active, .nav-link.active {
    color: #fff;
} */

.catbar .nav-item {
    -webkit-transition: color .2s ease-in-out;
    -o-transition: color .2s ease-in-out;
    transition: color .2s ease-in-out;
}

.nav-item:hover {
    color: #fff;
    text-shadow: 2px 1px 2px #343a40;
    -webkit-transition: color .2s ease-in-out;
    -o-transition: color .2s ease-in-out;
    transition: color .2s ease-in-out;
}

.nav-pills .nav-link.active, .nav-pills .show>.nav-link {
    background: #ff5f6d; /* Old browsers */ /* FF3.6-15 */
    background: -webkit-linear-gradient(45deg, #ff5f6d 0%,#ffc371 100%); /* Chrome10-25,Safari5.1-6 */
    background: -o-linear-gradient(45deg, #ff5f6d 0%,#ffc371 100%);
    background: linear-gradient(45deg, #ff5f6d 0%,#ffc371 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#ff5f6d', endColorstr='#ffc371',GradientType=1 ); /* IE6-9 fallback on horizontal gradient */
    -webkit-box-shadow: 0px 0px 8px rgba(50,50,50,0.28);
            box-shadow: 0px 0px 8px rgba(50,50,50,0.28);  
    border: 1px solid #b98286; 
    color: #fff;
    text-shadow: 2px 1px 2px #343a40;
}


