@import url('https://fonts.googleapis.com/css?family=Quicksand:500,700');

body {
  margin: 0;
  padding: 0;
  font-family: 'Quicksand', sans-serif;
  font-weight: 500;
  color: #2b2b2b;
}

.App {
  background: #d66d75; /* Old browsers */ /* FF3.6-15 */
  background: -webkit-linear-gradient(left, #d66d75 0%,#e29587 100%); /* Chrome10-25,Safari5.1-6 */
  background: -webkit-gradient(linear, left top, right top, from(#d66d75),to(#e29587));
  background: -o-linear-gradient(left, #d66d75 0%,#e29587 100%);
  background: linear-gradient(to right, #d66d75 0%,#e29587 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#d66d75', endColorstr='#e29587',GradientType=1 ); /* IE6-9 */
}

.mainContainer {
  padding-bottom: 20px;
  -webkit-box-shadow: 1px 1px 6px rgba(50,50,50,0.28);
          box-shadow: 1px 1px 6px rgba(50,50,50,0.28);
  background: #ff5f6d; /* Old browsers */ /* FF3.6-15 */
  background: -webkit-linear-gradient(45deg, #ff5f6d 0%,#ffc371 100%); /* Chrome10-25,Safari5.1-6 */
  background: -o-linear-gradient(45deg, #ff5f6d 0%,#ffc371 100%);
  background: linear-gradient(45deg, #ff5f6d 0%,#ffc371 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#ff5f6d', endColorstr='#ffc371',GradientType=1 ); /* IE6-9 fallback on horizontal gradient */
  min-height: 75vh;
}

a {
  color: inherit;
}

a:hover {
  color: inherit;
  text-decoration: none;
}


